@font-face {
  font-family: "CenturyGothic";
  src: url("../fonts/centurygothic.woff2") format("woff2"), url("../fonts/centurygothic.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "CenturyGothic";
  src: url("../fonts/centurygothic_bold.woff2") format("woff2"), url("../fonts/centurygothic_bold.woff") format("woff");
  font-style: normal;
  font-weight: bold;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

body {
  background-color: #fff;
  line-height: 125%;
  font-size: 16px;
  font-family: "CenturyGothic", Arial, sans-serif;
  color: #000;
}

a {
  text-decoration: none;
  color: inherit;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1430px;
  margin: 0 auto;
  padding: 0 15px;
}

html {
  scroll-behavior: smooth;
}

.header {
  background-color: #79000F;
  padding: 8px 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.header__inner {
  position: relative;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__top-logo-img {
  max-height: 100px;
}
.header__top-btn {
  padding: 13px 10px 13px 40px;
  position: relative;
  padding-left: 30px;
  border: 2px solid #fff;
  border-radius: 10px;
  color: #fff;
  font-weight: bold;
  transition: all 0.3s ease;
}
.header__top-btn::after {
  content: "";
  width: 16px;
  height: 16px;
  background: url("../images/tel-white.svg") no-repeat center center;
  background-size: contain;
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}
.header__top-btn:hover {
  background-color: #fff;
  color: #79000F;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.header__top-btn:hover::after {
  filter: brightness(0) saturate(100%) invert(11%) sepia(91%) saturate(4272%) hue-rotate(349deg) brightness(87%) contrast(113%);
}
.header__nav {
  position: absolute;
  right: 0;
  bottom: -50px;
}
.header__menu {
  display: flex;
  gap: 60px;
  width: 100%;
}
.header__menu-item {
  color: #202020;
  font-weight: bold;
  position: relative;
  transition: color 0.3s ease;
}
.header__menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #79000F;
  transition: width 0.3s ease;
}
.header__menu-item:hover {
  color: #79000F;
}
.header__menu-item:hover::after {
  width: 100%;
}

.main-section {
  height: 140vh;
  background: url("../images/main-bg.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top left;
  position: relative;
  min-height: 1100px;
}

.about {
  scroll-padding-top: -150px;
  height: 70vh;
  background: url("../images/about-bg.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.about__content-text {
  transform: translateY(-50%);
  color: #fff;
  max-width: 45%;
  line-height: 200%;
  text-align-last: left;
}
.about__content-text:nth-child(2) {
  transform: translateY(-50%);
  text-align: right;
  margin-left: auto;
}

.portfolio {
  padding: 50px 0;
}
.portfolio__title {
  font-size: 64px;
  font-weight: bold;
  opacity: 0.75;
  text-align: center;
  line-height: 100%;
  margin-bottom: 50px;
}
.portfolio__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 700px;
}
.portfolio__item img {
  max-height: 600px;
  transition: all 0.3s ease;
  cursor: pointer;
}
.portfolio__item:hover img {
  transform: scale(1.05);
  filter: brightness(1.1);
}
.portfolio__item:nth-child(2) img {
  max-height: 700px;
}
.portfolio__inner {
  position: relative;
}
.portfolio__items-arrow {
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(50%);
  z-index: 2;
  width: 25px;
  height: 95px;
  transition: all 0.3s ease;
  opacity: 0.7;
}
.portfolio__items-arrow:hover {
  opacity: 1;
  transform: translateY(50%) scale(1.1);
}
.portfolio__items-arrow--left {
  left: -55px;
}
.portfolio__items-arrow--left:hover {
  transform: translateY(50%) scale(1.1) translateX(-3px);
}
.portfolio__items-arrow--right {
  right: -55px;
}
.portfolio__items-arrow--right:hover {
  transform: translateY(50%) scale(1.1) translateX(3px);
}

.services {
  background-color: #79000F;
}
.services__inner {
  display: flex;
}
.services__column {
  width: 25%;
  padding: 65px 70px;
}
.services__logo {
  width: 220px;
  margin-bottom: 65px;
}
.services__list {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.services__list-item {
  font-size: 36px;
  line-height: 150%;
  color: #B79C5F;
  transition: all 0.3s ease;
  cursor: default;
  position: relative;
  padding-left: 0;
}
.services__list-item::before {
  content: "";
  position: absolute;
  left: -20px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 2px;
  background-color: #B79C5F;
  transition: width 0.3s ease;
}
.services__list-item:hover {
  color: #fff;
  padding-left: 25px;
}
.services__list-item:hover::before {
  width: 15px;
}
.services__description {
  width: 75%;
  background: url("../images/services-bg.webp") no-repeat center center;
  background-size: cover;
  padding: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
}
.services__description-text {
  color: #fff;
  font-size: 22px;
  line-height: 200%;
}

.contact {
  padding: 75px 0;
}
.contact__block {
  padding: 30px 35px;
  background-color: #0B1119;
  max-width: 770px;
  margin: 0 auto;
  color: #fff;
}
.contact__block-title {
  font-size: 48px;
  font-weight: 400;
  line-height: 200%;
}
.contact__block-subtitle {
  margin-bottom: 60px;
}
.contact__block-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.contact__block-info {
  max-width: 30%;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}
.contact__block-info-title {
  font-size: 20px;
  padding-left: 32px;
  position: relative;
  margin-bottom: 15px;
}
.contact__block-info-title::after {
  content: "";
  width: 26px;
  height: 17px;
  background: url("../images/email-white.svg") no-repeat center center;
  background-size: contain;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.contact__block-info-adress {
  line-height: 150%;
}
.contact__block-info-media {
  display: flex;
  gap: 20px;
  margin-top: auto;
}
.contact__block-info-media-item {
  width: 30px;
  height: 30px;
  transition: all 0.3s ease;
  cursor: pointer;
}
.contact__block-info-media-item img {
  width: 30px;
  height: 30px;
  transition: all 0.3s ease;
}
.contact__block-info-media-item:hover {
  transform: translateY(-3px) scale(1.1);
}
.contact__block-info-media-item:hover img {
  filter: brightness(1.3);
}
.contact__block-form {
  margin-left: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px 30px;
}
.contact__block-form-field {
  background-color: #00020A;
  padding: 16px 10px;
  border: none;
  color: #fff;
  transition: all 0.3s ease;
}
.contact__block-form-field:focus {
  outline: none;
  background-color: #0a1525;
  box-shadow: 0 0 0 2px rgba(183, 156, 95, 0.3);
}
.contact__block-form-field--textarea {
  grid-column: span 2;
  grid-row: span 2;
  resize: none;
}
.contact__block-form-field--btn {
  margin-left: auto;
  padding: 16px 30px;
  grid-column: span 2;
  color: #9c9c9c;
  cursor: pointer;
}
.contact__block-form-field--btn:hover {
  color: #fff;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}
.contact__block-form-field--btn:active {
  transform: translateY(0);
}

.footer {
  background-color: #79000F;
  color: #B79C5F;
  padding: 40px 0;
}
.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__links {
  display: flex;
  flex-direction: column;
  gap: 25px;
  font-size: 20px;
  font-weight: 700;
}
.footer__links a {
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}
.footer__links a::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #fff;
  transition: width 0.3s ease;
}
.footer__links a:hover {
  color: #fff;
  padding-left: 10px;
}
.footer__links a:hover::after {
  width: 100%;
}
.footer__media {
  display: flex;
  gap: 20px;
  margin-left: auto;
  margin-right: 30px;
}
.footer__media a {
  transition: all 0.3s ease;
  display: block;
}
.footer__media a img {
  transition: all 0.3s ease;
}
.footer__media a:hover {
  transform: translateY(-5px) rotate(5deg);
}
.footer__media a:hover img {
  filter: brightness(1.3);
}
.footer__logo img {
  width: 150px;
  transition: all 0.3s ease;
}
.footer__logo:hover img {
  transform: scale(1.05);
  filter: brightness(1.1);
}
.footer__copy {
  color: #fff;
  text-align: center;
  margin-top: 50px;
}

@media (min-width: 1440px) {
  .container {
    max-width: 1400px;
  }
}
@media (max-width: 1439px) {
  .portfolio__items-arrow--left {
    left: -40px;
  }
  .portfolio__items-arrow--right {
    right: -40px;
  }
}
@media (max-width: 1199px) {
  .header__menu {
    gap: 40px;
  }
  .header__nav {
    bottom: -40px;
  }
  .header__top-logo-img {
    max-height: 80px;
  }
  .main-section {
    min-height: 800px;
    height: 120vh;
  }
  .about {
    height: auto;
    padding: 80px 0;
  }
  .about__content-text {
    transform: translateY(0) !important;
    max-width: 100%;
    margin-bottom: 30px;
  }
  .about__content-text:nth-child(2) {
    margin-left: 0;
    text-align: left;
  }
  .portfolio__title {
    font-size: 52px;
  }
  .portfolio__items {
    height: auto;
    gap: 20px;
  }
  .portfolio__item img {
    max-height: 450px;
  }
  .portfolio__item:nth-child(2) img {
    max-height: 550px;
  }
  .portfolio__items-arrow--left {
    left: -30px;
  }
  .portfolio__items-arrow--right {
    right: -30px;
  }
  .services__column {
    padding: 50px 40px;
  }
  .services__list-item {
    font-size: 30px;
  }
  .services__description {
    padding: 60px;
  }
  .services__description-text {
    font-size: 20px;
  }
  .contact__block-title {
    font-size: 40px;
  }
  .footer__links {
    font-size: 18px;
  }
  .footer__logo img {
    width: 120px;
  }
}
@media (max-width: 991px) {
  .header {
    position: relative;
  }
  .header__nav {
    position: relative;
    bottom: 0;
    margin-top: 20px;
  }
  .header__menu {
    gap: 30px;
    font-size: 14px;
    justify-content: center;
  }
  .header__top-btn {
    padding: 10px 8px 10px 28px;
    font-size: 14px;
  }
  .header__menu-link {
    color: #fff;
  }
  .main-section {
    min-height: 600px;
    height: 100vh;
    margin-top: 0;
  }
  .portfolio__title {
    font-size: 42px;
    margin-bottom: 30px;
  }
  .portfolio__item img {
    max-height: 400px;
    width: 100%;
    object-fit: cover;
  }
  .portfolio__item:nth-child(2) img {
    max-height: 400px;
  }
  .portfolio__items-arrow {
    display: none;
  }
  .services__inner {
    flex-direction: column;
  }
  .services__column {
    width: 100%;
    padding: 40px 30px;
    text-align: center;
  }
  .services__logo {
    margin: 0 auto 40px;
  }
  .services__list {
    gap: 30px;
    align-items: center;
  }
  .services__list-item {
    font-size: 28px;
  }
  .services__list-item::before {
    display: none;
  }
  .services__list-item:hover {
    padding-left: 0;
  }
  .services__description {
    width: 100%;
    padding: 50px 30px;
    gap: 30px;
  }
  .services__description-text {
    font-size: 18px;
  }
  .contact__block {
    max-width: 90%;
  }
  .contact__block-content {
    flex-direction: column;
    gap: 40px;
  }
  .contact__block-info {
    max-width: 100%;
    margin-right: 0;
  }
  .contact__block-info-media {
    justify-content: center;
    margin-top: 30px;
  }
  .contact__block-form {
    margin-left: 0;
  }
  .footer__inner {
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
  }
  .footer__links {
    order: 2;
    width: 100%;
    align-items: center;
  }
  .footer__media {
    order: 1;
    margin: 0;
  }
  .footer__logo {
    order: 3;
  }
}
@media (max-width: 767px) {
  .main-section {
    min-height: 400px;
    height: 400px;
  }
  .header__top {
    flex-direction: column;
    gap: 15px;
  }
  .header__top-logo-img {
    max-height: 70px;
  }
  .header__menu {
    flex-wrap: wrap;
    gap: 20px;
    font-size: 13px;
  }
  .about {
    padding: 60px 0;
  }
  .about__content-text {
    font-size: 14px;
    line-height: 180%;
  }
  .portfolio {
    padding: 30px 0;
  }
  .portfolio__title {
    font-size: 36px;
  }
  .portfolio__item img {
    max-height: 300px;
  }
  .services__column {
    padding: 30px 20px;
  }
  .services__logo {
    width: 180px;
    margin-bottom: 30px;
  }
  .services__list-item {
    font-size: 24px;
  }
  .services__description {
    padding: 40px 20px;
  }
  .services__description-text {
    font-size: 16px;
  }
  .contact {
    padding: 50px 0;
  }
  .contact__block {
    padding: 25px 20px;
  }
  .contact__block-title {
    font-size: 32px;
  }
  .contact__block-subtitle {
    font-size: 14px;
    margin-bottom: 40px;
  }
  .contact__block-form {
    grid-template-columns: 1fr;
  }
  .contact__block-form-field--textarea {
    grid-column: span 1;
  }
  .contact__block-form-field--btn {
    grid-column: span 1;
    margin-left: 0;
  }
  .footer {
    padding: 30px 0;
  }
  .footer__links {
    gap: 15px;
    font-size: 16px;
  }
  .footer__copy {
    font-size: 12px;
    margin-top: 30px;
  }
}
@media (max-width: 575px) {
  .header__top-logo-img {
    max-height: 60px;
  }
  .header__top-btn {
    font-size: 12px;
    padding: 8px 6px 8px 24px;
  }
  .header__top-btn::after {
    width: 14px;
    height: 14px;
    left: 6px;
  }
  .header__menu {
    gap: 15px;
    font-size: 12px;
  }
  .main-section {
    min-height: 300px;
    height: 300px;
  }
  .about {
    padding: 40px 0;
  }
  .about__content-text {
    font-size: 13px;
    line-height: 170%;
  }
  .portfolio__title {
    font-size: 28px;
    margin-bottom: 20px;
  }
  .portfolio__item img {
    max-height: 250px;
  }
  .services__column {
    padding: 25px 15px;
  }
  .services__logo {
    width: 150px;
    margin-bottom: 25px;
  }
  .services__list {
    gap: 20px;
  }
  .services__list-item {
    font-size: 20px;
  }
  .services__description {
    padding: 30px 15px;
    gap: 20px;
  }
  .services__description-text {
    font-size: 14px;
    line-height: 180%;
  }
  .contact {
    padding: 40px 0;
  }
  .contact__block {
    padding: 20px 15px;
  }
  .contact__block-title {
    font-size: 26px;
    line-height: 150%;
  }
  .contact__block-subtitle {
    font-size: 13px;
    margin-bottom: 30px;
  }
  .contact__block-info-title {
    font-size: 16px;
  }
  .contact__block-info-adress {
    font-size: 14px;
  }
  .contact__block-form-field {
    font-size: 14px;
    padding: 12px 8px;
  }
  .footer {
    padding: 25px 0;
  }
  .footer__links {
    font-size: 14px;
  }
  .footer__links a:hover {
    padding-left: 5px;
  }
  .footer__media {
    gap: 15px;
  }
  .footer__media img {
    width: 25px;
    height: 25px;
  }
  .footer__logo img {
    width: 100px;
  }
  .footer__copy {
    font-size: 11px;
    margin-top: 20px;
  }
}
@media (max-width: 375px) {
  .header__menu {
    gap: 10px;
    font-size: 11px;
  }
  .portfolio__title {
    font-size: 24px;
  }
  .portfolio__item img {
    max-height: 200px;
  }
  .services__list-item {
    font-size: 18px;
  }
  .contact__block-title {
    font-size: 22px;
  }
}